import React, { Component } from 'react';
import { Card, Table, Pagination, message, Icon} from 'antd';
import HandleStateModel from './HandleStateModel';
import SearchBox from './SearchBox';
import {postAction} from '../../axios';
import './style.less'

let typeLists = [
    {code: null, name: '全部'},
    {code: 1, name: '失联预警'},
    {code: 2, name: '网络预警'},
    {code: 3, name: '挂科预警'},
    {code: 4, name: '消费预警'},
    {code: 5, name: '夜归预警'},
    {code: 6, name: '逃课预警'},
];

class ComprehensiveWarning extends Component {
    constructor(props) {
        super(props);
        const colors = ['#e45a5a','#f5a623','#f8e71c','#309cff'];
        
        const columns = [{
            title: '序号',dataIndex: 'rowIndex',align:'center',width: "45px",
        }, {
            title: '学号',dataIndex: 'studentId',align:'center',width: "8%", render: (text) => {return (<span title={text}>{text}</span>)}
        }, {
            title: '姓名',dataIndex: 'studentName',align:'center',width: "8%", render: (text) => {return (<span title={text}>{text}</span>)}
        }, {
            title: '性别',dataIndex: 'gender',align:'center',width: "4%",
        }, {
            title: '一卡通',dataIndex: 'schoolCard',align:'center',width: "8%", render: (text) => {return (<span title={text}>{text}</span>)}
        }, {
            title: '学院',dataIndex: 'collegeName',align:'center',width: "6%", render: (text) => {return (<span title={text}>{text}</span>)}
        }, {
            title: '专业',dataIndex: 'majorName',align:'center',width: "6%", render: (text) => {return (<span title={text}>{text}</span>)}
        }, {
            title: '年级',dataIndex: 'grade',align:'center',width: "6%",
        }, {
            title: '预警类别',dataIndex: 'alarmType',align:'center',width: "6%",
            render:(text, record)=>{
                let val = typeLists[text] ? typeLists[text].name:'';
                return (<span title={val}>{val}</span>)
            }
        }, {
            title: '预警级别',dataIndex: 'alarmLevel',align:'center',width: "6%",
            render:(text, record)=>{
                const backgroundColor = colors[text-1];
                return (
                    <span className={'circle-mark'} style={{width:16,height:16,top:4,backgroundColor}}/>
                )
            }
        }, {
            title: '预警描述',dataIndex: 'alarmDepict',align:'center', render: (text) => {return (<span title={text}>{text}</span>)}
        }, {
            title: '预警次数',dataIndex: 'warningNumber',align:'center',width: "6%",
        }, {
            title: '更新时间',dataIndex: 'updateDate',align:'center',width: "8%", render: (text) => {return (<span title={text}>{text}</span>)}
        }, {
            title: '状态',dataIndex: 'status',align:'center',width: "6%",
            render:(text, record)=>{
                let color,val;
                if(parseInt(text,10)===0){
                    color = '#e45a5a';val = '待处理';
                } else {
                    color = '#71cf09';val = '已处理';
                }
                return (<div className={"handleBtn"} style={{color}}>{val}</div>);
            },
            onCell:(record)=>{
                return {
                    onClick:(e)=>this.handleClick(e,record)
                }
            }
        }, {
            title: '处理结果',dataIndex: 'handleResult',align:'center', width: "6%", render: (text) => {return (<span title={text}>{text}</span>)}
        }];
        this.state = ({
            pageNum: 1,
            pageSize: 10,
            total: 0,
            columns,
            levelConfig: [],
            dataLists: [],
            filter: {},
            visible: false,
            curRowInfo: undefined
        });
    }
    componentWillMount(){
        this.getData();
    }
    getData(data={pageInfo: {pageNum: this.state.pageNum, pageSize: this.state.pageSize}}){
        // console.log(this.state.filter)
        data={
            ...data,
            ...this.state.filter
        }
        postAction("/bigdata/alarmTrigger/getPageData", data).then(
            (res) => {
                if (res.success) {
                    this.setState({
                        total: res.obj.total,
                        dataLists: res.obj.list || []
                    })
                } else {
                    message.warn(res.obj);
                }
            }
        )
    }
    refreshData(filter,isHandle){
        if(isHandle){
            let data = Object.assign({},{pageInfo:{pageNum:this.state.pageNum,pageSize:this.state.pageSize}},this.state.filter)
            this.getData(data)
            return;
        }
        this.setState({filter},()=>{
            let data = Object.assign({},{pageInfo:{pageNum:this.state.pageNum,pageSize:this.state.pageSize}},filter)
            // console.log(data)
            this.getData(data)
        })
    }
    handleClick(e,record){
        e.stopPropagation();
        if(record.status===0){
            this.setState({visible:true,curRowInfo:record});
        }
    }
    changePage(pageNum, pageSize) { //分页
        this.setState({
            pageNum, pageSize
        }, () => {
            let data = Object.assign({},{pageInfo:{  pageNum, pageSize }},this.state.filter)
            this.getData(data);
        })
    }
    rowClick(record, index){
        // console.log(record, index);
        // console.log(this.props)
        this.props.history.push('/mainframe/comprehensivewarning/detail/'+record.studentNo);
    }
    handleState = ()=> {
        this.setState({visible:false},()=>this.getData())
    }
    render(){

        const data = this.state.dataLists || [];
        data.forEach((item,index)=>{
            data[index].key = index;
            data[index].rowIndex = index+1;
            data[index]['studentId'] = item.student.studentId;
            data[index]['studentNo'] = item.student.studentNo;
            data[index]['studentName'] = item.student.name;
            data[index]['gender'] = item.student.gender;
            data[index]['majorName'] = item.student.majorName;
            data[index]['collegeName'] = item.student.collegeName;
            data[index]['schoolCard'] = item.student.studentNo;
            data[index]['grade'] = item.student.currentGrade;
            data[index]['warningNumber'] = item.historyAmount;

        })
        return (
            <React.Fragment>
                <Card title="预警列表" extra={<a className="overviewBtn" href="#/mainframe/comprehensivewarning/overview"><Icon type="area-chart" theme="outlined" />总体概况</a>}>
                    <SearchBox refreshData={(data)=>this.refreshData(data)} typeLists={typeLists} />
                    <div style={{ marginTop: 30 }}>
                        <Table
                            rowClassName="clickRow"
                            columns={this.state.columns}
                            dataSource={data}
                            bordered
                            size="middle"
                            pagination={false}
                            onRow={(record, index) => {
                                return {
                                  onClick: () => this.rowClick(record, index),// 点击行
                                };
                            }}
                        />
                        <div className={'tableTip'}>
                            预警级别说明:
                            <span className={'circle-mark'}/> 红色预警
                            <span className={'circle-mark'} style={{backgroundColor:'#f5a623'}}/> 橙色预警
                            <span className={'circle-mark'} style={{backgroundColor:'#f8e71c'}}/> 黄色预警
                            <span className={'circle-mark'} style={{backgroundColor:'#309cff'}}/> 蓝色预警
                        </div>
                        <Pagination style={{marginTop:20, textAlign: 'right'}} showQuickJumper defaultCurrent={1}
                            // defaultPageSize={this.state.pageSize}
                            total={this.state.total}
                            onChange={this.changePage.bind(this)}
                            // onShowSizeChange={this.changePage.bind(this)} 
                            showTotal={total => `共 ${total} 条数据`}
                            size="small"
                            />
                    </div>
                </Card>
                {this.state.visible?(
                    <HandleStateModel 
                        alarmTriggerId={this.state.curRowInfo&&this.state.curRowInfo.id}
                        onCancel={()=>this.setState({visible:false})}
                        onOk={this.handleState}
                    />
                ):null}
            </React.Fragment>
        );
    }
}

export default ComprehensiveWarning;


