import React, { Component } from 'react';
import { Row, Col, Select, Button, Input, DatePicker } from 'antd'
// import SelectYear from '../../common/components/SelectYear/SelectYear';
import {postAction} from '../../axios';
import {hostUrl} from "../../env";


const Option = Select.Option;
const RangePicker = DatePicker.RangePicker;


// const currentGrade = new Date().getFullYear();
// let gradeLists = [{ code: "", name: "全部年级"}];
// for (let i=0; i<6; i++) {
//     gradeLists.push({
//         name: currentGrade - i + "级",
//         code: currentGrade - i + ""
//     })
// }

class SearchBox extends Component {
    constructor(props) {
        super(props);
        let typeLists = [
            { code: null, name: '全部' },
            { code: 1, name: '失联预警' },
            { code: 2, name: '网络预警' },
            { code: 3, name: '挂科预警' },
            { code: 4, name: '消费预警'},
            { code: 5, name: '夜归预警'},
            { code: 6, name: '逃课预警'},
        ];
        let levelLists = [
            { code: null, name: '全部' },
            { code: 1, name: '红色预警' },
            { code: 2, name: '橙色预警' },
            { code: 3, name: '黄色预警' },
            { code: 4, name: '蓝色预警' }
        ];
        let statusLists = [
            { code: null, name: '全部' },
            { code: 0, name: '未处理' },
            { code: 1, name: '已处理' },
            // { code: 'mark', name: '旗标预警' }
        ];
        this.state = ({
            collegeLists: [],
            majorLists: [],
            studentTypeLists: [],
            typeLists,
            levelLists,
            statusLists,
            college: "",
            major: "",
            studentType: "",
            currentGrade: "",
            currentGradeName: "全部年级",
            gradeLists:[
                { code: "", name: "全部年级"},
                { code: 2018, name: "2018级"},
                { code: 2017, name: "2017级"},
                { code: 2016, name: "2016级"},
                { code: 2015, name: "2015级"},
                { code: 2014, name: "2014级"},
            ],
            type: "",
            level: "",
            status: "",
            flag: "",
            studentNo: "",
            startDate: "",
            endDate: "",
            filter: {
                currentGrade: "",
                currentGradeName: "全部年级",
                gradeLists:[
                    { code: "", name: "全部年级"},
                    { code: 2018, name: "2018级"},
                    { code: 2017, name: "2017级"},
                    { code: 2016, name: "2016级"},
                    { code: 2015, name: "2015级"},
                    { code: 2014, name: "2014级"},
                ],
            }
        });
    }
    componentWillMount() {
        this.getCollegeLists();
    }
    getCollegeLists() {
        postAction('/bigdata/school/listCollege').then(res=>{
            if(res.success){
                this.setState({ collegeLists: res.obj });
            }
        })
    }
    getMajorLists(collegeCode) {
        if (collegeCode === null) {
            this.setState({ majorLists: [] });
            return;
        }
        let data = {collegeCode}
        postAction('/bigdata/school/listMajorByCollege',data).then(res=>{
            if(res.success){
                this.setState({ majorLists:res.obj });
            }
        })
    }
    changeFilter(value, teamType ) {
        switch (teamType) {
            case 0:
                this.setState({ currentGrade: value }); break;
            case 1:
                this.setState({ college: value, major: undefined }, () => this.getMajorLists(value)); break;
            case 2:
                this.setState({ major: value }); break;
            case 3:
                this.setState({ studentType: value }); break;
            case 4:
                this.setState({ type: value }); break;
            case 5:
                this.setState({ level: value }); break;
            case 6:
                if (value === 'mark') {
                    this.setState({ flag: 1, status: value });
                } else {
                    this.setState({ status: value, flag: null });
                }
                break;
            case 7:
                this.setState({ studentNo: value }); break;
            case 8:
                this.setState({ startDate: value[0], endDate: value[1] }); break;
            default:
                break;
        }
    }
    searchAction() {
        const {
            currentGrade,
            college: collegeCode,
            major: majorCode,
            type: alarmType,
            level: alarmLevel,
            status,
            flag,
            studentNo,
            startDate,
            endDate } = this.state;
        let filter = { alarmType, alarmLevel, status: status === 'mark' ? undefined : status, flag, student:{studentNo,currentGrade, collegeCode, majorCode}, startDate, endDate };
        this.props.refreshData(filter);
    }

    handleExport=()=>{
        let {currentGrade,college,major,studentNo,type,level,status,startDate,endDate}=this.state;
        window.location.href=`${hostUrl}/bigdata/alarmTrigger/exportAlarm?grade=${currentGrade}&collegeCode=${college}&majorCode=${major}&studentNo=${studentNo}&alarmType=${type}&alarmLevel=${level}&status=${status}&startDate=${startDate}&endDate=${endDate}`
    };

    render() {

        return (
            <div>
                <Row>
                    <Col span={8}>
                        <Row type={'flex'} align={'middle'}>
                            <Col span={8} className="text-center">年级:</Col>
                            <Col span={16}>

                                <Select className="mr-10" style={{ width: '100%' }}
                                    placeholder={"全部年级"}
                                    value={this.state.currentGrade}
                                    onChange={(value) => this.changeFilter(value, 0)}
                                >
                                    {
                                        this.state.gradeLists.map((item, index) => <Option key={index} value={item.code}>{item.name}</Option>)
                                    }
                                </Select>
                            </Col>
                        </Row>
                    </Col>
                    <Col span={8}>
                        <Row type={'flex'} align={'middle'}>
                            <Col span={8} className="text-center">院系:</Col>
                            <Col span={16}>
                                <Select className="mr-10" style={{ width: '100%' }}
                                    placeholder="选择院系"
                                    value={this.state.college}
                                    onChange={(value) => { this.changeFilter(value, 1) }}>
                                    {this.state.collegeLists.map((item, index) => <Option key={index} value={item.collegeCode}>{item.collegeName}</Option>)}
                                </Select>
                            </Col>
                        </Row>
                    </Col>
                    <Col span={8}>
                        <Row type={'flex'} justify={'center'} align={'middle'}>
                            <Col span={8} className="text-center">专业:</Col>
                            <Col span={16}>
                                <Select className="mr-10" style={{ width: '100%' }}
                                    placeholder="选择专业"
                                    value={this.state.major}
                                    onChange={(value) => { this.changeFilter(value, 2) }}>
                                    {this.state.majorLists.map((item, index) => <Option key={index} value={item.majorCode}>{item.majorName}</Option>)}
                                </Select>
                            </Col>
                        </Row>
                    </Col>
                    {/* <Col span={6}>
                        <Row type={'flex'} justify={'center'} align={'middle'}>
                            <Col span={8} className="text-center">学生类型:</Col>
                            <Col span={16}>
                                <Select className="mr-10" style={{ width: '100%' }}
                                    placeholder="选择学生类型"
                                    value={this.state.studentType}
                                    onChange={(value) => { this.changeFilter(value, 3) }}>
                                    {this.state.studentTypeLists.length>0?<Option key={'all'} value={null}>全部</Option>:null}
                                    {this.state.studentTypeLists.map((item, index) => <Option key={index} value={item.teamId}>{item.name}</Option>)}
                                </Select>
                            </Col>
                        </Row>
                    </Col> */}
                </Row>
                <Row style={{ marginTop: 16 }}>
                    <Col span={8}>
                        <Row type={'flex'} justify={'center'} align={'middle'}>
                            <Col span={8} className="text-center">预警类别:</Col>
                            <Col span={16}>
                                <Select className="mr-10" style={{ width: '100%' }}
                                    placeholder="选择预警类别"
                                    value={this.state.type}
                                    onChange={(value) => { this.changeFilter(value, 4) }}>
                                    {this.state.typeLists.map((item, index) => <Option key={index} value={item.code}>{item.name}</Option>)}
                                </Select>
                            </Col>
                        </Row>
                    </Col>
                    <Col span={8}>
                        <Row type={'flex'} justify={'center'} align={'middle'}>
                            <Col span={8} className="text-center">预警级别:</Col>
                            <Col span={16}>
                                <Select className="mr-10" style={{ width: '100%' }}
                                    placeholder="选择预警级别"
                                    value={this.state.level}
                                    onChange={(value) => { this.changeFilter(value, 5) }}>
                                    {this.state.levelLists.map((item, index) => <Option key={index} value={item.code}>{item.name}</Option>)}
                                </Select>
                            </Col>
                        </Row>
                    </Col>
                    <Col span={8}>
                        <Row type={'flex'} justify={'center'} align={'middle'}>
                            <Col span={8} className="text-center">预警状态:</Col>
                            <Col span={16}>
                                <Select className="mr-10" style={{ width: '100%' }}
                                    placeholder="选择预警状态"
                                    value={this.state.status}
                                    onChange={(value) => { this.changeFilter(value, 6) }}>
                                    {this.state.statusLists.map((item, index) => <Option key={index} value={item.code}>{item.name}</Option>)}
                                </Select>
                            </Col>
                        </Row>
                    </Col>
                </Row>
                <Row style={{ marginTop: 16 }}>
                    <Col span={8}>
                        <Row type={'flex'} justify={'center'} align={'middle'}>
                            <Col span={8} className="text-center">学号/姓名:</Col>
                            <Col span={16}>
                                <Input className="mr-10"
                                    value={this.state.studentNo}
                                    style={{ width: '100%' }}
                                    placeholder="请输入学生学号或一卡通号或姓名"
                                    onChange={(e) => { this.changeFilter(e.target.value, 7) }} />
                            </Col>
                        </Row>
                    </Col>
                    <Col span={16}>
                        <Row type={'flex'} justify={'center'} align={'middle'}>
                            <Col span={4} className="text-center">时间:</Col>
                            <Col span={14}>
                                <RangePicker
                                    style={{ width: '100%' }}
                                    onChange={(moment, values) => {
                                        this.changeFilter(values, 8)
                                    }} />
                            </Col>
                            <Col span={4} className={"text-right"}>
                                <Button icon="search" type={'primary'} onClick={() => this.searchAction()}>搜索</Button>
                            </Col>
                            <Col span={2} className={"text-right"}>
                                <Button type={'primary'} onClick={this.handleExport} >导出</Button>
                            </Col>
                        </Row>
                    </Col>
                </Row>
            </div>
        )
    }
}

export default SearchBox;


